import React, { useState } from "react";

export default function InputDate(props) {
    const [error, setError] = useState(false);
    const [textError, setTextError] = useState('  ');
    const [date, setDate] = useState({ start: null, end: null });

    function onChangeValue(e) {

        if (e == null) return;

        if (e.target.classList.contains('input--date-start')) {

            setDate(prevState => ({
                ...prevState,
                start: e.target.value
            }));

            validate(e.target.value, date.end);
            props.getInputValue(e.target.value, 'start-date');
        }

        if (e.target.classList.contains('input--date-end')) {
            props.getInputValue(e.target.value, 'start-end');

            setDate(prevState => ({
                ...prevState,
                end: e.target.value
            }));

            validate(date.start, e.target.value);
            props.getInputValue(e.target.value, 'start-end');
        }

    }

    function validate(valueStart, valueEnd) {
        const res = [valueStart, valueEnd];
        const today = formatDate(new Date());

        for (let i = 0; i < res.length; i++) {
            if (res[i] > today) {
                setError(true);
                setTextError('      ');
                return;
            } else {
                setError(false);
            }

        }


        if (valueStart > valueEnd) {
            setError(true);
            setTextError('       ');
        } else {
            setError(false);
        }
    }

    function formatDate(date) {

        let dd = date.getDate();
        if (dd < 10) dd = '0' + dd;

        let mm = date.getMonth() + 1;
        if (mm < 10) mm = '0' + mm;

        let yy = date.getFullYear();
        if (yy < 10) yy = '0' + yy;

        return yy + '-' + mm + '-' + dd;
    }

    return (
        <div className="search__wrap-item">
            <label className="search__label label">  <sup>*</sup></label>
            <div className="search__wrap-input">
                <input
                    onChange={onChangeValue}
                    type="date"
                    className="search__input input input--date-start"
                    name="date" />
                <input
                    onChange={onChangeValue}
                    type="date"
                    className="search__input input input--date-end"
                    name="date2" />
                <p className={"search__error error " + (error ? '' : 'hide')}>{textError}</p>
            </div>
        </div>
    )
}